/*    HEADER    */

.header{
    background: url('../images/header2.png') no-repeat center top / cover;
}

.header__container{
    min-height: 108vh;
    padding-top: 70px;
}

.header__top{
    height: 75px;
    position: relative;
}

.header__burger-btn{
    background-image: url('../images/menu.png');
    background-size: cover;
    width: 39px;
    height: 21px;
    position: fixed;
    right: 50px;
    top: 50px;
    display: none;
}

.header__navigation{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__logo{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
}

.header__right-block{
    min-width: 420px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.header__right-block::before{
    content: "";
    display: block;
    background-image:url('../images/Line.png');
    background-size: 47px 2px;
    width: 47px;
    height: 2px;
    position: absolute;
    bottom: 50%;
    left: 54%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
}

.header__content{
    margin-top: 100px;
    display: flex;
    justify-content: center;
}

.header__btn{
    min-width: 280px;
    position: relative;
}

.header__btn::after{
    content: "";
    display: block;
    background-image:url('../images/Arrow.png');
    background-size: 30px 16px;
    width: 30px;
    height: 16px;
    position: absolute;
    top: 50%;
    right: 15%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
}